@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans-bold.woff2") format("woff2");
}
img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  color: #000000;
  background-color: #ffffff;
}

.button {
  text-decoration: none;
  color: #000000;
  display: block;
  padding: 15px;
  box-sizing: border-box;
  font-family: inherit;
  font-size: inherit;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid #444444;
  cursor: pointer;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
  border-color: transparent;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--special {
  background-color: #63d1bb;
  border-color: transparent;
  color: #ffffff;
}
.button--special:hover {
  background-color: #56C3B2;
}

.main-header {
  padding-top: 20px;
  background-color: #ffffff;
}

.main-header__wrapper {
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
  width: 320px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.main-header__logo {
  margin: 0 auto;
  margin-bottom: 20px;
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-header {
    padding-bottom: 20px;
    border-bottom: 1px solid #F1F1F1;
  }

  .main-header__wrapper {
    margin-left: auto;
    margin-right: auto;
    padding-left: 50px;
    padding-right: 50px;
    width: 768px;
    box-sizing: border-box;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .main-header__logo {
    margin: 0;
    margin-right: 30px;
    flex-shrink: 0;
  }
}
@media (min-width: 1150px) {
  .main-header__wrapper {
    width: 1150px;
  }
}
.main-nav {
  position: relative;
}

.main-nav__toggle {
  position: absolute;
  right: 0;
  top: -50px;
  width: 20px;
  height: 20px;
  padding: 0;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 2px;
  border: none;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  right: -2px;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--closed .main-nav__wrapper {
  display: none;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-weight: 700;
}

@media (min-width: 768px) {
  .main-nav {
    font-size: 14px;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav--closed .main-nav__wrapper {
    display: flex;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    font-size: 18px;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
}

.site-list__link {
  text-decoration: none;
  color: #000000;
  display: block;
  padding: 30px 0;
