.user-list {
  display: flex;
  flex-direction: column;
}

.user-list__link {
  text-decoration: none;
  color: #000000;
  display: block;
  padding: 30px 0;
  padding-left: 45px;
  position: relative;
}
.user-list__link:hover {
  opacity: 0.6;
}
.user-list__link:active {
  opacity: 0.3;
